import RestApi from '../rest-api';
import {inject} from 'aurelia-framework';
import {Router} from 'aurelia-router';

@inject(Router)
export class ProjectDetail{
    constructor(router){
        this.api = new RestApi();
        this.organizationId;
        this.organizations;
        this.title;
        this.description;
        this.project;
        this.activities;
        this.allowOvertime;
        this.billOvertime;
        this.name;
        this.router = router;
    }

    async activate(params){
        this.title = "Project aanmaken"
        this.organizations = JSON.parse(await this.api.getOrganizations());
        if(params.orgId){
            this.organizationId = params.orgId;
            if(params.id){
                this.project = JSON.parse(await this.api.getProject(this.organizationId, params.id));
            }
        }
        if (this.project !== undefined && this.project != null){
            this.fillForm(this.project);
        }
    }

    async fillForm(project){
        this.title = project.name;
        this.name = project.name;
        this.description = project.description;
        this.activities = JSON.parse(await this.api.getActivities(this.organizationId,project.id));
    }

    confirmedColor(value){
        if(value){
            return "confirmed";
        }else{
            return "notConfirmed";
        }
    }

    confirmed(value){
        if (value){
            return "&check;";
        }else{
            return "&chi;";
        }
    }

    async updateProject(){
        var body = {'name':this.name,
            'description':this.description,
            'allowOvertime':this.allowOvertime,
            'billOvertime':this.billOvertime
        };
        if(this.project){
            this.api.updateProject(this.organizationId,this.project.id, body) 
        }else{
            var result = JSON.parse(await this.api.createProject(this.organizationId,body));
            this.router.navigate('projects/'.concat(result.organization.id).concat(result.id));
        }
    }
    
    backToList(){
        this.router.navigate('projects');
    }
}